@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;800&display=swap');

body {
  font-family: 'Poppins', sans-serif;
  background-image: url("../public/bg.png");
  background-repeat: repeat;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

body::before {
  content: '';
  inset: 0;
  position: fixed;
  pointer-events: none;
  box-shadow: 0 0 500px 0 black inset;
}

h1 {
  font-size: 3rem;
  color: white;
  margin-bottom: 2rem;
  margin-top: -20px;
}

.game-block {
  gap: 2rem;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
}

.card {
  cursor: pointer;
  width: 150px;
  height: 170px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform-style: preserve-3d;
  transition: transform .7s;
}

.card.matched {
  transform: rotateY(180deg);
}

.card img,
.card svg {
  width: 70%;
  position: absolute;
  backface-visibility: hidden;
  pointer-events: none;
}

.card svg {
  width: 100px;
  height: 100px;
  opacity: .3;
}

button {
  border: none;
  color: black;
  background-color: white;
  cursor: pointer;
}

button.start-game {
  text-transform: uppercase;
  font-weight: 800;
  padding: 1.5rem 2rem;
  letter-spacing: 2px;
  font-size: 25px;
  border-radius: 10px;
  background-color: white;
  transition: all 1s ease;
}

button.reset {
  display: flex;
  align-items: center;
  justify-content: center;
  /*background-color: red;*/
  height: 80px;
  width: 80px;
  position: fixed;
  top: 4rem;
  right: 4rem;
  border-radius: 50%;
  transition: all .5s;
}

button.start-game:hover{
  background-color: black;
  transition: .5s ease-out;
  color: white;
}

button.reset:hover {
  background-color: #829191;
  transition: all .5s ease-in-out;
}

.lives span{
  color: white;
  position: absolute;
  top: 0;
  left: 0;
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 30px;
  margin-top: 40px;
  margin-left: 40px;
}

.lives {
  top: 0;
  left: 0;
  position: absolute;
  gap: 10px;
  margin-top: 70px;
  margin-left: 80px;
  display: flex;
  flex-direction: column;
}

.heart{
  height: 80px;
}

.heart.fade-out {
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.sound_img {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 40px;
  margin-right: 70px;
  margin-bottom: 105px;
}

.sound_img_base {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 90px;
  margin-right: 120px;
  margin-bottom: 80px;
}

.sound_img_base:hover {
  cursor: pointer;
}

.reset--is-resetting path:first-of-type {
  animation: rotate 1s ease-in-out forwards;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(90deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.winStreak {
  color:white;
  font-size: 70px;
  font-family: 'Poppins';
  font-weight: bold;
  position: absolute;
  right: 0;
  margin-right: 128px;
  margin-top: 195px;
}

.winstreak {
  height: 100px;
  position: absolute;
  right: 0;
  margin-right: 40px;
  margin-top: 180px;
}

.arrows {
  height: 150px;
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 140px;
  transform: rotate(20deg);
  opacity: 0;
}


